.container {
  margin: 15px;
  width: 300px;
  height: calc(100% - 50px);
  background-color: #32393d;
  position: fixed;
  display: flex;
  flex-direction: column;
  border-radius: 5px; }

.navHeading {
  color: #85898b;
  font-size: 1.3rem;
  margin: 2rem 0 1rem 1rem; }

.sidebarHeading {
  padding: 1.5rem 0;
  border-bottom: 1px solid #535657; }

.sidebar {
  padding: 0 0.5rem;
  width: 100%;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #535657; }
